.deleteBox {
  display: flex;
  align-items: center;
  position: absolute;
  background-color: white;
  border: 1px solid var(--border-gray);
  border-radius: 4px;
  top: 56px;
  right: 12px;
  padding: 8px 12px;
  box-shadow: 2px 2px 10px 3px #0000001a;

  span {
    cursor: pointer;
  }

  .gray {
    color: var(--secondary-gray);
  }
}
